.estnltk{
  font-size:1.5em;
  border-radius:5px;
  border-style:none;
  user-select: none;
  line-height:1.5em;

}

.estnltk .words{

    cursor:pointer;
  z-index: 100;

  background-color: rgb(255, 255, 255);
  border-radius:5px;
  border-style:solid;
  border-width:2px;
  border-color:darkgrey;
}

