.estnltk .named_entities[data-label="ORG"]{
  background-color:lightgreen;
}

.estnltk .named_entities[data-label="LOC"]{
  background-color:dodgerblue;
}
.estnltk .named_entities[data-label="PER"]{
  background-color:pink;
}


