  background-color:lightgray;
  z-index: 100;

}


.estnltk .sentences{
  padding:4px;
  background-color: rgba(100, 100, 0, 0.1);
  border-radius:5px;
  border-style:solid;
  border-color: rgba(100, 100, 0, 0.31);
}


